<template lang="html">
  <div class="zc">
    <div class="headers-wrapper" style="border-bottom: 1px solid #e6e9f2;">
      <headers></headers>
    </div>
    <!-- <div class="vis"></div> -->
    <div class="form-box">
      <div class="form-wrapper">
        <el-form label-width="120px">
          <el-form-item>
            <h3>承运人注册</h3>
          </el-form-item>
          <el-form-item label="手机号" :rules="{ required: true}">
            <el-input placeholder="请输入手机号"></el-input>
          </el-form-item>
          <el-form-item label="密码" :rules="{ required: true}">
            <el-input type="password" placeholder="请输入密码"></el-input>
          </el-form-item>
          <el-form-item label="确认密码" :rules="{ required: true}">
            <el-input type="password" placeholder="请再次确认密码"></el-input>
          </el-form-item>
          <el-form-item label="手机验证码" :rules="{ required: true}">
            <el-input type="password" placeholder="请输入手机验证码"></el-input>
          </el-form-item>
        </el-form>
        <el-form label-width="120px">
          <el-form-item>
            <h3 class="cen">企业信息</h3>
          </el-form-item>
          <el-form-item label="企业名称" :rules="{ required: true}">
            <el-input placeholder="请输入企业名称"></el-input>
          </el-form-item>
          <el-form-item label="企业地址" :rules="{ required: true}">
            <el-input placeholder="请输入企业地址"></el-input>
          </el-form-item>
          <el-form-item label="负责人" :rules="{ required: true}">
            <el-input placeholder="请输入负责人"></el-input>
          </el-form-item>
          <el-form-item label="传真">
            <el-input></el-input>
          </el-form-item>
          <el-form-item label="运营证书" :rules="{ required: true}">
            <el-input placeholder="请输入运营证书号"></el-input>
          </el-form-item>
          <el-form-item label="营业执照" :rules="{ required: true}">
            <el-input placeholder="请上传营业执照"></el-input>
          </el-form-item>
          <el-form-item label="其他资质证书">
            <el-input placeholder="请上传其他资质证书"></el-input>
          </el-form-item>
          <el-form-item prop="resource">
            <el-radio-group v-model="ruleForm.resource">
              <el-radio label="阅读并同意"><a href="javascript:;" style="color:deepskyblue;font-size:12px;">《软件许可及服务协议》</a></el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item>
            <router-link class="el-button" type="danger" :to="{path:'/index'}">注册</router-link>
            <!-- <el-button></el-button> -->
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>

<script>
import headers from "@/components/header"
export default {
  components:{
    headers
  },
  data() {
      return {
        ruleForm: {
          resource: ''
        }
      }
    }
}
</script>

<style lang="less">
  .zc{
    .form-box{
      padding: 30px 0 0 0;
      background: #F5F6FB;
      min-width: 1200px;
    }
    .vis{
      height: 30px;
      background: #F5F6FA;
    }
    h3{
      text-align: center;
      font-size: 20px;
      color: #2e51a5;
      &.cen{
        margin-top: 0px;
      }
    }
    .form-wrapper{
      width: 1200px;
      margin: 0 auto;
      border: 1px solid #e6e9f2;
      background: white;
      padding: 30px 0 40px 0;
      .el-form{
        width: 40%;
        margin: 0 auto;
        .el-button{
          width: 100%;
          font-weight: bold;
          background: #F45C44;
          color: white;
          outline: none;
          border: none;
        }
        .el-form-item__label{
          color: #8B98B5;
          font-weight: bolder;
        }
      }
    }

  }
</style>



